
export function replaceNonMatchingCharacters(originalText: string) {
    const replacementTable = {
        '': 'i',
        '': 'c',
        ' ':'_'
    };

    originalText = originalText.toLocaleLowerCase();
    // Replacement table' kullanarak metindeki kriterlere uymayan karakterleri deitir
    var replacedText = originalText.replace(/[^a-zA-Z0-9._-]/g, function (match) {
        return replacementTable[match] || match; // Eer replacement table'da varsa deitir, yoksa ayn karakteri koru
    });

    return replacedText;
}